.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1001;
  background-color: rgba(0, 0, 0, 0.8);
  overflow: auto;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.is-shown {
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.modal-window {
  font-family: Roboto;
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 101;
  width: 280px;
  padding: 48px 20px 41px;
  transform: translate(-50%, -50%);
  background-color: #ffffff;
  overflow: auto;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
    transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.modal-window__close-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  top: 10px;
  right: 10px;
  position: absolute;
  width: 24px;
  height: 24px;
  border: none;
  background: #ffffff;
  cursor: pointer;
  transition: transform var(--duration) var(--timing-function);

  &:hover,
  &:focus {
    transform: scale(1.3);
  }
}
.header-phone_title {
  @include font(24px, 21px);
  font-weight: 500;
  text-align: center;
  margin-bottom: 17px;
}

.call-button__list {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  & > :last-child {
    margin-bottom: 18px;
  }
}
.call-button__item {
  @include font(24px, 28px);
  font-weight: 500;
  margin-bottom: 13px;
  color: #2f4377;
}
.header-phone_soc {
  @include font(18px, 21px);
  font-weight: 500;
  text-align: center;
  margin-bottom: 17px;
}

.backdrop .img-viber {
  width: 32px;
  height: 32px;
  margin-right: 11px;
}
.backdrop .img-telega {
  width: 32px;
  height: 32px;
  margin-right: 0;
}
.backdrop .footer__soc-list {
  display: flex;
  justify-content: center;
  margin-bottom: 0;
}

@include min-width('medium') {
  .modal-window {
    top: 50%;
    left: 50%;
    width: 438px;
    padding: 42px 29px 40px;
    transform: translate(-50%, -50%);
  }
}
